import React, { Component } from "react";
import { withTranslation } from "react-i18next";
import { inject, observer } from "mobx-react";
import styles from "./style.module.scss";
import Loading from "../../common/Widget/Loading";
import Nodata from "../../common/Widget/Nodata";
import { Icon, Tooltip, Avatar, message, Badge, Col, Row } from "antd";
import dayjs from "dayjs";
import { getFile, imgLoad } from "../../util/util";
import { host, wsget } from "../../ws";
import NewFeedback from "./NewFeedback";
import BigimgCpt from "../Home/BigImg";
import { observable } from "mobx";

@withTranslation()
@inject("feedback", "login")
@observer
class Feedback extends Component {
  constructor(props) {
    super(props);
    this.ulRef = React.createRef();
    this.boxRef = React.createRef();
    this.state = {
      avatar: false,
      add: null,
      confirmLoading: false,
      bImg_visible: false,
      imgfile: {},
      files: null,
    };
  }
  @observable inner = false;
  @observable visible = false;

  componentDidMount() {
    this.props.feedback.getData();
    this.resize();
    window.addEventListener("resize", this.resize);
  }

  componentWillUnmount() {
    this.props.login.getUser_detail();
    window.removeEventListener("resize", this.resize);
  }

  resize = () => {
    if (window.innerWidth < 576) {
      if (!this.inner) {
        this.inner = true;
      }
    } else {
      if (this.inner) {
        this.inner = false;
        this.visible = false;
      }
    }
  };

  showModal = (add) => {
    this.setState({
      visible: true,
      add,
    });
  };

  handleOk = (data, cb) => {
    this.setState({
      confirmLoading: true,
    });
    if (!this.state.add) {
      data.id = this.props.feedback.id;
    }
    wsget("feedback_save", data, 1)
      .then((res) => {
        this.props.feedback.refreshtDetail(res);
      })
      .finally(() => {
        this.setState({
          visible: false,
          confirmLoading: false,
        });
        cb && cb();
      });
  };

  handleCancel = (cb) => {
    this.setState({
      add: null,
      visible: false,
    });
    cb && cb();
  };

  closeDialog = () => {
    this.setState({
      bImg_visible: false,
      files: null,
    });
  };

  onPiece = () => {
    if (this.imgidx - 1 >= 0) {
      this.onBigImg(this.state.files, this.imgidx - 1);
    } else {
      this.imgidx = this.state.files.length - 1;
      this.onBigImg(this.state.files, this.imgidx);
      message.warning(this.props.t("lastOne"));
    }
  };

  nextPage = () => {
    if (this.imgidx + 1 >= this.state.files.length) {
      this.imgidx = 0;
      this.onBigImg(this.state.files, this.imgidx);
      message.warning(this.props.t("firstOne"));
      return;
    }
    this.onBigImg(this.state.files, this.imgidx + 1);
  };

  onBigImg = (files, i) => {
    const realNum = files.length;
    const img = { id: files[i] };
    this.imgidx = i;
    this.setState({ bImg_visible: true, imgfile: img, files });
    if (i + 1 < realNum) {
      imgLoad(getFile.file_b(files[i + 1]));
    }
    if (!(i - 1 < 0)) {
      imgLoad(getFile.file_b(files[i - 1]));
    }
  };

  back = () => {
    this.visible = false;
  };

  render() {
    const {
      init,
      id,
      listData,
      detail,
      detailInit,
      empty,
      subject,
      created,
      getDetail,
      user_id,
    } = this.props.feedback;
    const { t } = this.props;
    const { visible, add, confirmLoading, bImg_visible, imgfile } = this.state;

    return (
      <div className={styles.feedback_inner}>
        {init ? (
          <Row style={{ height: "100%" }}>
            <Col
              xs={{ span: 24 }}
              sm={{ span: 9 }}
              md={{ span: 8 }}
              lg={{ span: 6 }}
              xl={{ span: 5 }}
              style={{ height: "100%" }}
            >
              {this.visible ? (
                <div className={styles.feedback_right}>
                  {detailInit ? (
                    empty ? (
                      <Nodata />
                    ) : (
                      <div className={styles.feedback_right_inner}>
                        <div className={styles.feedback_r_head}>
                          <p
                            className={`${styles.r_head_title} ellipsis`}
                            onClick={this.back}
                          >
                            <Icon type="left" />
                            {subject}
                          </p>
                          <p className={styles.r_head_bottom}>
                            <span>id: {id}</span>
                            <span>{dayjs(created).format(t("LL"))}</span>
                          </p>
                        </div>
                        <div className={styles.r_bottom_body} ref={this.boxRef}>
                          <ul ref={this.ulRef}>
                            {detail.map((item, i) => {
                              return (
                                <li key={i} className={styles.detailList}>
                                  {item.user_id === user_id ? (
                                    <p className={styles.list}>
                                      <span>
                                        {dayjs(item.created).format(t("LL"))}
                                      </span>
                                      <span>
                                        我&nbsp;&nbsp;
                                        {item.profile ? (
                                          <Avatar
                                            src={`${host()}/uf/s/${
                                              item.profile.id
                                            }/${item.user_id}`}
                                          />
                                        ) : (
                                          <Avatar icon="user" />
                                        )}
                                      </span>
                                    </p>
                                  ) : (
                                    <p className={styles.list}>
                                      <span>
                                        {item.unread && (
                                          <Badge status="error" />
                                        )}
                                        客服&nbsp;&nbsp;
                                        {item.profile ? (
                                          <Avatar
                                            src={`${host()}/uf/s/${
                                              item.profile.id
                                            }/${item.user_id}`}
                                          />
                                        ) : (
                                          <Avatar icon="user" />
                                        )}
                                      </span>
                                      <span>
                                        {dayjs(item.created).format(t("LL"))}
                                      </span>
                                    </p>
                                  )}
                                  <p>{item.message}</p>
                                  {item.attach && (
                                    <div className={styles.attach}>
                                      <ul>
                                        {item.attach.map((el, i) => {
                                          return (
                                            <li
                                              key={el}
                                              onClick={() => {
                                                this.onBigImg(item.attach, i);
                                              }}
                                            >
                                              <img
                                                src={
                                                  item.user_id === user_id
                                                    ? getFile.file_s(el)
                                                    : getFile.feedback_s(
                                                        el,
                                                        item.user_id
                                                      )
                                                }
                                                alt=""
                                              />
                                            </li>
                                          );
                                        })}
                                      </ul>
                                    </div>
                                  )}
                                </li>
                              );
                            })}
                          </ul>
                        </div>
                        <div
                          className={styles.bottom_btn}
                          onClick={() => {
                            this.showModal(null);
                          }}
                        >
                          <Icon type="message" />
                          回复
                        </div>
                      </div>
                    )
                  ) : (
                    <Loading />
                  )}
                </div>
              ) : (
                <div className={styles.feedback_left}>
                  <div className={styles.feedback_l_head}>
                    <span>{t("FeedbackHistory")}</span>
                    <Tooltip placement="bottom" title={t("addFeedback")}>
                      <Icon
                        type="plus"
                        onClick={() => {
                          this.showModal("add");
                        }}
                      />
                    </Tooltip>
                  </div>
                  <div className={styles.feedback_l_body}>
                    <ul>
                      {listData.map((item) => {
                        return (
                          <li
                            className={`${styles.l_listcell} ${
                              id === item.id ? styles.l_active : ""
                            }`}
                            key={item.id}
                            onClick={() => {
                              if (this.inner) {
                                this.visible = true;
                                getDetail(item);
                              } else {
                                getDetail(item);
                              }
                            }}
                          >
                            <div className={styles.subject}>
                              <Badge count={item.unread} />
                              {item.subject}
                            </div>
                            <p className={styles.message}>{item.message}</p>
                            <p className={styles.created}>
                              {dayjs(item.created).format(t("LL"))}
                            </p>
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                </div>
              )}
            </Col>
            <Col
              xs={{ span: 0 }}
              sm={{ span: 15 }}
              md={{ span: 16 }}
              lg={{ span: 18 }}
              xl={{ span: 19 }}
              style={{ height: "100%" }}
            >
              <div className={styles.feedback_right}>
                {detailInit ? (
                  empty ? (
                    <Nodata />
                  ) : (
                    <div className={styles.feedback_right_inner}>
                      <div className={styles.feedback_r_head}>
                        <p className={`${styles.r_head_title} ellipsis`}>
                          {subject}
                        </p>
                        <p className={styles.r_head_bottom}>
                          <span>id: {id}</span>
                          <span>{dayjs(created).format(t("LL"))}</span>
                        </p>
                      </div>
                      <div className={styles.r_bottom_body} ref={this.boxRef}>
                        <ul ref={this.ulRef}>
                          {detail.map((item, i) => {
                            return (
                              <li key={i} className={styles.detailList}>
                                {item.user_id === user_id ? (
                                  <p className={styles.list}>
                                    <span>
                                      {dayjs(item.created).format(t("LL"))}
                                    </span>
                                    <span>
                                      {t("me")}&nbsp;&nbsp;
                                      {item.profile ? (
                                        <Avatar
                                          src={`${host()}/uf/s/${
                                            item.profile.id
                                          }/${item.user_id}`}
                                        />
                                      ) : (
                                        <Avatar icon="user" />
                                      )}
                                    </span>
                                  </p>
                                ) : (
                                  <p className={styles.list}>
                                    <span>
                                      {item.unread && <Badge status="error" />}
                                      客服&nbsp;&nbsp;
                                      {item.profile ? (
                                        <Avatar
                                          src={`${host()}/uf/s/${
                                            item.profile.id
                                          }/${item.user_id}`}
                                        />
                                      ) : (
                                        <Avatar icon="user" />
                                      )}
                                    </span>
                                    <span>
                                      {dayjs(item.created).format(t("LL"))}
                                    </span>
                                  </p>
                                )}
                                <p>{item.message}</p>
                                {item.attach && (
                                  <div className={styles.attach}>
                                    <ul>
                                      {item.attach.map((el, i) => {
                                        return (
                                          <li
                                            key={el}
                                            onClick={() => {
                                              this.onBigImg(item.attach, i);
                                            }}
                                          >
                                            <img
                                              src={
                                                item.user_id === user_id
                                                  ? getFile.file_s(el)
                                                  : getFile.feedback_s(
                                                      el,
                                                      item.user_id
                                                    )
                                              }
                                              alt=""
                                            />
                                          </li>
                                        );
                                      })}
                                    </ul>
                                  </div>
                                )}
                              </li>
                            );
                          })}
                        </ul>
                      </div>
                      <div
                        className={styles.bottom_btn}
                        onClick={() => {
                          this.showModal(null);
                        }}
                      >
                        <Icon type="message" />
                        {t('reply')}
                      </div>
                    </div>
                  )
                ) : (
                  <Loading />
                )}
              </div>
            </Col>
          </Row>
        ) : (
          <Loading />
        )}

        <NewFeedback
          visible={visible}
          add={add}
          handleOk={this.handleOk}
          confirmLoading={confirmLoading}
          handleCancel={this.handleCancel}
        />

        {bImg_visible && (
          <BigimgCpt
            closeDialog={this.closeDialog}
            imgfile={imgfile}
            onPiece={this.onPiece}
            nextPage={this.nextPage}
          />
        )}
      </div>
    );
  }
}

export default Feedback;
