import React, { PureComponent } from "react";
import { Modal, Button, Upload, Icon, message } from "antd";
import styles from "./style.module.scss";
import { changeHash, ajaxPromise, getCookie, getFile } from "../../util/util";
import { host } from "../../ws";
import i18next from "i18next";

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
}

export default class NewFeedback extends PureComponent {
  constructor(props) {
    super(props);
    this.textareaRef = React.createRef();
    this.inputRef = React.createRef();
    this.state = {
      previewVisible: false,
      previewImage: "",
      fileList: [],
      upLoading: false,
    };
  }

  previewHandleCancel = (e) => {
    this.setState({ previewVisible: false });
  };

  handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    this.setState({
      previewImage: file.url || file.preview,
      previewVisible: true,
    });
  };

  handleChange = ({ fileList }) => this.setState({ fileList });

  url = (file) => {
    const obj = { file };
    return new Promise(async (resolve, reject) => {
      try {
        const hash = await changeHash(obj, { quality: 0.7 }, 2048, 2048);
        const url = `${host()}/up/p/${getCookie("cookie")}/${hash.data.hash}/${
          file.name
        }`;
        resolve(url);
      } catch (error) {
        reject(error);
      }
    });
  };

  uploadImg = async ({ file, action }) => {
    const { fileList } = this.state;
    const imgItem = {
      uid: file.uid, // 注意，这个uid一定不能少，否则上传失败
      name: "hehe.png",
      status: "uploading",
      url: "",
      percent: 50, // 注意不要写100。100表示上传完成
    };
    this.setState({
      fileList: [...fileList, imgItem],
      upLoading: true,
    });
    try {
      const data = await ajaxPromise(file, action);
      const imgItem = {
        uid: file.uid, // 注意，这个uid一定不能少，否则上传失败
        name: file.name,
        status: "done",
        url: getFile.file_s(data.id),
        id: data.id,
      };
      if (this.props.visible) {
        this.setState({
          fileList: [...fileList, imgItem],
          upLoading: false,
        });
      }
    } catch (error) {
      message.error(i18next.t("feebfaid"));
      this.setState({
        upLoading: false,
      });
    }
  };

  Ok = () => {
    let data;
    const attach = [];
    const subject = this.props.add ? this.inputRef.current.value.trim() : true;
    const msg = this.textareaRef.current.value.trim();
    if (!subject) {
      message.error(i18next.t("feedWarning1"));
      return;
    }
    if (!msg) {
      message.error(i18next.t("feedWarning2"));
      return;
    }
    if (this.props.add) {
      data = {
        subject,
        message: msg,
      };
    } else {
      data = {
        message: msg,
      };
    }

    if (this.state.fileList.length) {
      this.state.fileList.forEach((item) => {
        attach.push(item.id);
      });
      data.attach = attach;
    }
    if (this.state.upLoading) {
      message.warning(i18next.t("feedWarning3"));
    } else {
      this.props.handleOk(data, this.Cancel);
    }
  };

  handleCancel = (e) => {
    this.props.handleCancel(this.Cancel);
  };

  Cancel = () => {
    this.textareaRef.current.value = "";
    this.setState({
      fileList: [],
      upLoading: false,
    });
  };

  render() {
    const { visible, add, confirmLoading } = this.props;
    const { previewVisible, previewImage, fileList, upLoading } = this.state;
    const uploadButton = (
      <div>
        <Icon type="plus" />
      </div>
    );
    return (
      <Modal
        centered
        title={i18next.t("ProblemFeedback")}
        visible={visible}
        onOk={this.Ok}
        onCancel={this.handleCancel}
        destroyOnClose={true}
        footer={
          add ? (
            <Button
              key="submit"
              type="primary"
              disabled={upLoading}
              loading={confirmLoading}
              onClick={this.Ok}
            >
              {i18next.t("Submit")}
            </Button>
          ) : (
            <Button
              key="submit"
              type="primary"
              disabled={upLoading}
              loading={confirmLoading}
              onClick={this.Ok}
            >
              {i18next.t("reply")}
            </Button>
          )
        }
      >
        {add && (
          <div className={`${styles.fb_block} clearfix`}>
            <div className={styles.fb_lable}>
              <span className={styles.fb_tips}>*</span>
              <span className={styles.fb_title_tips}>Title:</span>
            </div>
            <div className={styles.fb_textarea}>
              <input
                placeholder={i18next.t("enterTitle")}
                maxLength={250}
                type="text"
                ref={this.inputRef}
              />
            </div>
          </div>
        )}
        <div className={`${styles.fb_block} clearfix`}>
          <div className={styles.fb_lable}>
            <span className={styles.fb_tips}>*</span>
            <span className={styles.fb_title_tips}>Message:</span>
          </div>
          <div className={styles.fb_textarea}>
            <textarea
              name="content"
              id="fb_des_content"
              placeholder={i18next.t("enterDdescription")}
              maxLength={1000}
              ref={this.textareaRef}
            ></textarea>
          </div>
        </div>

        <div className={`${styles.fb_block} clearfix`}>
          <div className={styles.fb_lable1}>
            <span className={styles.fb_title_tips}>
              Attach: ({i18next.t("Upto10")})
            </span>
          </div>
          <div className={`clearfix`}>
            <Upload
              action={this.url}
              accept="image/bmp,image/gif,image/jpeg,image/png"
              customRequest={this.uploadImg}
              listType="picture-card"
              fileList={fileList}
              onPreview={this.handlePreview}
              onChange={this.handleChange}
            >
              {fileList.length >= 10 ? null : uploadButton}
            </Upload>
            <Modal
              visible={previewVisible}
              footer={null}
              bodyStyle={{textAlign:'center'}}
              onCancel={this.previewHandleCancel}
              centered
            >
              <img
                alt="example"
                style={{ maxWidth: "80vw", maxHeight: "70vh" }}
                src={previewImage}
              />
            </Modal>
          </div>
        </div>
      </Modal>
    );
  }
}
